import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { useParams, Navigate } from 'react-router-dom'
import { reset_password_confirm } from '../redux/actions'

export default function ResetPasswordConfirm() {
    const [isSend, setIsSend] = useState()
    const [formData, setFormData] = useState({
        new_password: '',
    })
    const dispatch = useDispatch()
    const { uid, token } = useParams()
    const onSubmit = (e) => {
         e.preventDefault()
        dispatch(reset_password_confirm(uid, token, formData.new_password))
        setIsSend(true)
    }
    if (isSend) {
        return <Navigate to={'/'} />
    }
    return (

        <div className="login-page">
            <h2 className="page-title">Reset password confirmation page</h2>
            <form className="form" onSubmit={onSubmit} method='post'>
                <label className="form-label">New Password:</label>
                <input
                    placeholder='Password'
                    className="form-input"
                    name='password'
                    type='password'
                    value={formData.new_password}
                    onChange={(e) => setFormData({ ...formData, new_password: e.target.value })}
                />
                <button className="form-button" type='submit'>Submit</button>
            </form>
        </div>
    )
}
