import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, Navigate } from 'react-router-dom'
import { login } from '../redux/actions'


export default function Login() {
    const isAuthenticated = useSelector(state => state.login.isAuthenticated)
    const error = useSelector(state => state.login.login_error)
    const dispatch = useDispatch()
    const [formData, setFormData] = useState({
        email: '',
        password: '',
    })

    const LoginSubmit = (e) => {
        e.preventDefault()
        dispatch(login(formData.email, formData.password))
    }
    const LoginChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        })
    }

    if (isAuthenticated) {
        return <Navigate to="/" />
    }

    return (
        <div className="login-page">
            <h2 className="page-title">Login page</h2>
            {error ? <span className="error-text"> {error}</span> : ''}

            <form className="form" onSubmit={(e) => LoginSubmit(e)} method='post'>
                <label className="form-label">Email:</label>
                <input
                    className="form-input"
                    placeholder='email'
                    name='email'
                    value={formData.email}
                    onChange={(e) => LoginChange(e)}
                />
                <label className="form-label">Password:</label>
                <input
                    className="form-input"
                    placeholder='password'
                    name='password'
                    type="password"
                    minLength={5}
                    onChange={(e) => LoginChange(e)}
                />
                <button className="form-button" type="submit">Submit</button>
                <div className="form-text">
                    <h4>Do you have not an account?</h4>
                    <Link to="/sign_up">Sign up</Link>
                </div>
                <div className="form-text">
                    <h4>Do you forget a password?</h4>
                    <Link to="/reset">Reset Password</Link>
                </div>
            </form>

        </div>
    )
}
